function resolveComWixpressLocaldeliverySpiV1LocalDeliveryProviderUrl(
  opts: Omit<ResolveUrlOpts, 'domainToMappings'>
) {
  const domainToMappings = {};

  return resolveUrl(Object.assign(opts, { domainToMappings }));
}

const PACKAGE_NAME = '@wix/auto_sdk_restaurants_local-delivery-spi';

/** Retrieves list of account belongs to given instance */
export function listAccountIds(payload: object): RequestOptionsFactory<any> {
  function __listAccountIds({ host }: any) {
    const metadata = {
      entityFqdn: 'wix.localdelivery.spi.v1.tpa',
      method: 'POST' as any,
      methodFqn:
        'com.wixpress.localdelivery.spi.v1.LocalDeliveryProvider.ListAccountIds',
      packageName: PACKAGE_NAME,
      migrationOptions: {
        optInTransformResponse: true,
      },
      url: resolveComWixpressLocaldeliverySpiV1LocalDeliveryProviderUrl({
        protoPath: '/account-ids',
        data: payload,
        host,
      }),
      data: payload,
    };

    return metadata;
  }

  return __listAccountIds;
}

