/** Retrieves an estimate of the delivery */
export function estimateDelivery(payload: object): RequestOptionsFactory<any> {
  function __estimateDelivery({ host }: any) {
    const serializedData = transformPaths(payload, [
      {
        transformFn: transformSDKTimestampToRESTTimestamp,
        paths: [{ path: 'pickupTime' }, { path: 'dropoffTime' }],
      },
      {
        transformFn: transformSDKFloatToRESTFloat,
        paths: [
          { path: 'pickupAddress.geocode.latitude' },
          { path: 'pickupAddress.geocode.longitude' },
          { path: 'dropoffAddress.geocode.latitude' },
          { path: 'dropoffAddress.geocode.longitude' },
        ],
      },
    ]);
    const metadata = {
      entityFqdn: 'wix.localdelivery.spi.v1.tpa',
      method: 'POST' as any,
      methodFqn:
        'com.wixpress.localdelivery.spi.v1.LocalDeliveryProvider.EstimateDelivery',
      packageName: PACKAGE_NAME,
      migrationOptions: {
        optInTransformResponse: true,
      },
      url: resolveComWixpressLocaldeliverySpiV1LocalDeliveryProviderUrl({
        protoPath: '/estimate-delivery',
        data: serializedData,
        host,
      }),
      data: serializedData,
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [
              { path: 'promisedPickupTime' },
              { path: 'promisedDropoffTime' },
            ],
          },
        ]),
    };

    return metadata;
  }

  return __estimateDelivery;
}

